import React,{ useState } from "react";//, 
import { Modal, useMediaQuery, Button } from "@material-ui/core";
import { makeStyles } from '@material-ui/styles';
const useStyles = makeStyles( theme => ({
  root: {
    height: 300,
    flexGrow: 1,
    minWidth: 300,
    transform: 'translateZ(0)',
    // The position fixed scoping doesn't work in IE 11.
    // Disable this demo to preserve the others.
    '@media all and (-ms-high-contrast: none)': {
      display: 'none',
    },
  },
  modal: {
    display: 'flex',
    position: "absolute",
    padding: "16, 31, 27",
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: 'white',
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)" 
  },
  paper: {
    width: 400,
    border: '2px solid #000',
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));


function ModalCreateOrder() {
  const classes = useStyles;
  // const rootRef = React.useRef(null);
  
  const [viewModal, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <>
      <Button type="button" onClick={handleOpen}>
        Create Order
      </Button>
      <Modal
        disablePortal
        className={classes.modal}
        disableEnforceFocus
        disableAutoFocus
        open={viewModal}
        onClose={handleClose}
        aria-labelledby="server-modal-title"
        aria-describedby="server-modal-description"
        >
        {/* container={() => rootRef.current} */}
        <div className={classes.paper}>
          <h2 id="server-modal-title" onClick={handleClose}>Server-side modal</h2>
          <p id="server-modal-description">If you disable JavaScript, you will still see me.</p>
        </div>
      </Modal>
    </>
  );
};

export default ModalCreateOrder;