import React, { Component, useEffect, useState } from "react";
import { Box, Typography, useMediaQuery, Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import PersonIcon from "@material-ui/icons/Person";
import OrderComponent from "../../components/Order";
import { postOrders } from "../../hooks/utils";
import "./Orders.scss";

const Orders = () => {
  const isMobile = useMediaQuery((theme) => theme.breakpoints.down("sm"));

  // =================================================================

  const handleSubmit = (data) => {
    postOrders(data);
  };

  const [formValues, setFormValues] = useState({
    name1: "",
    name2: "",
    name3: "",
    name4: "",
  });

  // return (
  //   <>
  //     <form onSubmit={handleSubmit}>

        

  //     </form>
  //   </>
  // );

  // =================================================================

  return (
    <>
      <div className="table__title"> Orders </div>
      <OrderComponent />
      {/* <Box py={20} textAlign="center">
        <Typography variant="h2">Orders Page</Typography>
      </Box> */}
      {isMobile ? (
        <Box textAlign="left">
          <Button
            component={Link}
            variant="contained"
            color="primary"
            to="/Products"
          >
            <ChevronLeftIcon />
            <Typography variant="button">Products</Typography>
            <PersonIcon style={{ marginLeft: 15 }} />
          </Button>
        </Box>
      ) : (
        <> </>
      )}
    </>
  );
};
export default Orders;
